.blog{
    .mainContainer{
        gap: 1rem;
        .singlePost{
            gap: 2rem;

            .imgDiv{
                height: 200px;
                width: 100%;
                border-radius: 1rem;
                border: 1px solid var(--grey-bg);
                box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.406);
                overflow: hidden;

                &:hover{
                    img{
                        transform: scale(1.1);
                        transition: .3s ease;
                    }
                }
                img{
                    height: 100%;
                    object-fit: cover;
                }
            }
            .postDetails{
                h3{
                    color: var(--black-color);
                    font-weight: 600;
                    padding-bottom:10px;
                }
                p{
                    font-size: 13px;
                    color: var(--text-color);
                    font-weight: 500;
                }
                
            }
            a{
                font-size: 15px;
                color: var(--first-color);
                font-weight: 600;
                padding-top: .5rem;
                width: max-content;
                &:hover{
                    color: grey;
                    font-weight: 600;

                    .icon{
                        transform: translateX(10px);

                    }
                }
            }
        }
    }
}

@media screen and (min-width: 884px){
    .blog{
        .mainContainer{
            grid-template-columns: repeat(2,1fr);
        }
    }
}