.popular{
    padding-top: 15rem;

    .secContainer{
        .secHeader{
            flex-direction: column;
            align-items: center;
            gap: 1rem;

            .textDiv{
                .secTitle{
                    color: var(--black-color);
                    line-height: 1.5rem;
                    padding-bottom: .5rem;
                }
                p{
                    font-size: 13px;
                    color: var(--text-color);
                    font-weight: 500;
                }
            }
            .iconsDiv{
                gap: .5rem;
                .icon{
                    padding: .5rem;
                    background: var(--grey-text);
                    border-radius: 50%;
                    font-size: 2.5rem;
                    &:hover{
                        background: var(--black-color);
                        column-rule: var(--white-color);

                    }
                }
                .leftIcon{
                    background: var(--black-color);
                    color: var(--white-color);
                    &:hover{
                        background: var(--grey-text);
                        color: var(--black-);
                    }
                }
            }
        }
    }

    .mainContent{
        padding: 2rem 0;
        gap: 1rem;

        .singleDestination{
            overflow: hidden;
            border-radius: 1rem;
            box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45);
            
            
            .destImage{
                height: 200px;
                width: 100%;
                position: relative;
                overflow: hidden;
                img{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
                .overlayInfo{
                    position: absolute;
                    display: flex;
                    align-items: flex-start;
                    padding: 0;
                    /* padding: 1rem 1.5rem; */
                    justify-content: center;
                    flex-direction: column;
                    background: linear-gradient(rgba(0,0,0,0.846), rgba(0,0,0,0.759));
                    bottom: 0;
                    width: 100%;
                   /*  height: 100%; */
                   height: 0;
                    overflow: hidden;

                    h3{
                        color: var(--white-color);
                        font-weight: 600;
                    }
                    p{
                        color: var(--white-color);
                        opacity: .7;
                        font-size: 13px;
                    }
                    .icon{
                        position: absolute;
                        right: 10px;
                        bottom: 10px;
                        padding: .5rem;
                        background: var(--first-color);
                        font-size: 2.5rem;
                        border-radius: 50%;
                        color: var(--white-color);
                        border: 1px solid transparent;
                        transition: .3s ease;

                        &:hover{
                            background:var(--black-color);
                            border: 1px solid var(--first-color);
                            color: var(--first-color);
                        }
                    }
                }
            }
            .destFooter{
                padding: 1rem;
                
                .number{
                    font-size: 2rem;
                    font-weight: 700;
                    color: var(--black-color);
                    position: relative;
                    width: max-content;

                    &::after{
                        content: '';
                        position: absolute;
                        background:var(--first-color);
                        top:45%;
                        right: -150%;
                        height: 5px;
                        width: 45px;
                        border-radius: 1rem;
                    }
                }
                .destText{
                    justify-content: space-between;

                    h6{
                        color: var(--black-color);
                        font-size: 15px;
                    }
                    .flex{
                        font-size: 16px;
                        font-weight: 600;
                        align-items: flex-start;
                        transform: translateY(5px);

                        .icon{
                            color: var(--first-color);
                        }
                    }
                }
            }

            &:hover{
                .overlayInfo{
                    overflow: visible;
                    padding: 1rem 1.5rem;
                    height: 100%;
                }
            }
        }
    }
}



@media screen and (min-width: 480px){
    .popular{
        padding-top:10rem;
        
    }
}
@media screen and (min-width: 556px){
    .popular{
        .secContainer{
            .secHeader{
                width: 100%;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                .textDiv{
                    text-align: start;
                    max-width: 70%;

                }
                .iconsDiv{
                    justify-content: flex-end;
                }
            }
        }
        
    }
}


@media screen and (min-width: 720px){
    .popular{
        padding-top: 7rem;
    }
}

@media screen and (min-width:1024px){
    .popular{
        padding-top: 10rem;

        .secContainer{
            .secHeader{
                .textDiv{
                    .secTitle{
                        font-size: 2.2rem;
                    }
                    p{
                        font-size: 15px;
                    }
                }
            }
            .mainContent{
                grid-template-columns: repeat(4,1fr);
                gap: 1.5rem !important;

            }
        }
    }
}
