.home{
    height: 90vh;
    background-image:url('../../assets/bg1.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; 
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    position: relative;

    .secContainer{
        margin-top: -7rem; 
        .homeText{
            text-align: center;
            .title{
                color: var(--white-color);
                font-size: 2rem;
                font-weight: 700;
                line-height: 2.5rem;
            }
            .subTitle{
                color: var(--white-color);
                opacity: .9;
                font-size: 13px;
                font-weight: 300;
                padding: 1rem 0;
                max-width: 70%;
                text-align: center;
                margin: auto;
            }
        }
        .homeCard{
            width: 80%;
            border-radius: 1rem;
            padding: 2rem;
            background: var(--white-color);
            position: absolute;
            gap: 1rem;
            justify-content: center;
            bottom: -35%;
            transform: translate(-50%);
            left: 50%;
            box-shadow: 0 2px 8px 4px rgba(178,178,178, 0.45);

            .locationDiv, .distDiv, .priceDiv{
                width: 100%;
                label{
                    display: block;
                    color: var(--text-color);
                    font-weight: 500;
                    font-size: 13px;
                    padding-bottom: .4rem;

                }
                input{
                    width: 100%;
                    background: var(--input-color);
                    border-radius: 10px;
                    border: none;
                    padding: 10px;
                    font-size: 13px;
                    font-weight: 500;

                    &::placeholder{
                        font-size: 13px;
                        opacity: .5;
                        font-weight: 400;
                    }
                    &:focus{
                        outline: none;
                    }
                }
            }

            .btn{
                width: 100%;
                padding: .6rem 1.5rem;
                color: var(--white-color);
                font-weight: 600;
            }
        }

    }
}
/* linear-gradient(rgba(33,33,33,0.522),rgba(33,33,33,0.522), */

@media screen and (min-width: 480px){
    .home{
        .secContainer{
            .homeCard{
                bottom: -100px;
                width: 90%;
                grid-template-columns: repeat(2,1fr);
                .btn{
                    transform: translateY(15px);
                }
            }
        }
    }
}
 @media screen and (min-width:556px){
    .home{
        .secContainer{
            .homeText{
                .title{
                    font-size: 2.5rem;
                }
                .subTitle{
                    font-size: 18px;

                }
            }
        }
    }
}
 @media screen and (min-width:720px){
    .home{
        .secContainer{
            .homeCard{
                bottom: -10%;
                grid-template-columns: repeat(4,1fr);
            }
        }
    }
}
 @media screen and (min-width:720px){
    .home{
        .secContainer{
            .homeText{
                padding: 3rem 2rem;

                .title{
                   
                    font-size: 3.2rem;
                    margin: auto ;
                    line-height: 4rem;
                }

                .subTitle{
                    width: 70%;
                }
            }
        }
    }
}
 @media screen and (min-width:1024px){
    .home{
        .secContainer{
            .homeText{
                .subTitle{
                    font-size: 20px;
                }
            }
            .homeCard{
                width: 70%;
            }
        }
    }
}
 