@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    transition: .3s;
}
:root{
    --first-color: hsl(26, 93%, 50%);
    --hover-first-color: hsl(26, 90%, 42%);
    --white-color: hsl(0, 0%, 100%);
    --black-color:hsl(0, 0%, 18%);
    --text-color:hsl(240, 1%, 48%);
    --white-color-deam: hsl(0, 0%, 93%);
    --grey-bg: hsl(0, 0%, 93%);
    --grey-text: rgb(190, 190, 190);
    --input-color:hsl(330, 12%, 97%);
    --hover-color: hsl(26,93%, 40%);
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}
.section{
    padding: 4rem 0 2rem;
}
.container{
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}
.icon{
    font-size: 2rem;
    cursor: pointer;
}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}
.btn{
    padding: .4rem 1.5rem;
    background-color: var(--first-color);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
}
.btn a{
    color: var(--white-color);
    font-weight: 500;
}
.btn:hover{
    background:var(--hover-color);
}
.btn:hover a{
    color: var(--white-color);
}
.secIntro{
    max-width: 70%;
    text-align: center;
    margin: 0 auto 2rem;
}
.secIntro .secTitle{
    color: var(--black-color);
    line-height: 1.5rem;
    padding-bottom: .5rem;
}
.secIntro p{
    font-size: 13px;
    color: var(--text-color);
    font-weight: 500;
}
img, video{
    width: 100%;
    height: auto;
}
input{
    font-size: 100%;
}

@media screen and (min-width: 556px){
    .mainContent, .singlePost{
        grid-template-columns: repeat(2,1fr);
        
    }
}
@media screen and (min-width: 1024px){
    .container{
        width: 75% !important;
        margin: auto !important;
    }
    .section{
        padding: 5rem 0 3rem;
    }
    .secIntro{
        margin:0 auto 3rem;
    }
    .secIntro .secTitle{
        line-height: 2rem;
        padding-bottom: 1rem;
        font-size: 15px;
    }
    .secIntro p{
        font-size: 15px;
    }
}


@media screen and (min-width: 2560px){
    body{
        zoom: 1.7;
    }
}

::selection{
    background: var(--first-color);
    color: var(--white-color);
}

::-webkit-scrollbar{
    width: 10px;
    background: var(--grey-bg);
}
::-webkit-scrollbar-thumb{
    background: var(--first-color);
    border-radius: 10px;
    cursor: pointer;
}